import React, { useState } from 'React'

const App = () => {
    
    const [ good, setGood ] = useState(0)
    const [ neutral, setNeutral ] = useState(0)
    const [ bad, setBad ] = useState(0)
    const [allClicks, setAll] = useState([])
    const Statistics = ({good,neutral,bad})



return (
    <div>
        <h1>Give feetback, please</h1>
            <div>
            {good}
            <button onClick={() => setGood(good + 1)}>
            good
            </button>
            {neutral}
            <button onClick={() => setNeutral(neutral + 1)}>
            neutral
            </button>
            {bad}
            <button onClick={() => setBad(bad + 1)}>
            bad
            </button>       
            </div>


        <h1>statistics</h1>
            <div>good {good.setGood}</div>
            <div>neutral {neutral.setNeutral}</div>
            <div>bad {bad.setBad}</div>


            <div>allClicks{allClicks}</div>
            <div>average {(good * 1 + bad * (-1)/allClicks)}</div>
            <div> positive feedback {(good/allClicks*100)}</div>
        <div>good 6</div>
        <div>neutral 2</div>
        <div>bad 1</div>


    </div>
)
}

export default App